<template>
	<section class="attestation">
		<div class="attestation-head">
			<!-- <span class="Supermarket">贷款超市</span> -->
			<div class="screen">
				<div :class="[errorClass1 ,isactive1 ? activeClass1 : '']" @click="leftClick">
					<span>贷款金额</span>
					<img v-if='leftImg == true' src="../images/down_aw_gary.png"/>
					<img v-if='leftImg == false' src="../images/down_aw_whtie.png"/>
				</div>
				<div  :class="[errorClass2 ,isactive2 ? activeClass2 : '']" @click="rightClick">
					<span>贷款期限</span>
					<img v-if='rightImg == true' src="../images/down_aw_gary.png"/>
					<img v-if='rightImg == false' src="../images/down_aw_whtie.png"/>
				</div>
			</div>
		</div>
		<div v-show='maneyBox' class="moneyBox">
			<div class="left">
				<span class="moneySpan" v-for='(item, index) in moneyList' @click='moneyClick(index)'>{{item}}</span>
			</div>
			<div @click='close' class="bottom"></div>
		</div>
		<div v-show='timeBox' class="timeBox">
			<div class="right">
				<span class="timeSpan" v-for='(item, index) in timeList' @click='timeClick(index)'>{{item}}</span>
			</div>
			<div @click='close' class="bottom"></div>
		</div>
		<div class="content">
			<mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="false">
				<ul>
					<li class="d" v-for='(item,index) in msg' @click='into(index)'>
						<img :src='item.picUrl'/>
						<span>{{item.pname}}</span>
						<span><span>{{item.pBorrowNum}}</span>人申请</span>
					</li>
				</ul>
			</mt-loadmore>
		</div>
	</section>
  
</template>
<script>
	import { getFlowPlatformInfo, getUser } from './../api/api'
	// import { setStore } from '../config/mUtils'
	import { mapMutations } from 'vuex'
	import { getStore } from '../config/mUtils'
export default {
  data () {
    return {
    	errorClass1: 'left',
    	activeClass1: 'left1',
    	isactive1: false,
    	leftImg: true,
    	errorClass2: 'right',
    	activeClass2: 'right1',
    	isactive2: false,
    	rightImg: true,
    	msg: [
		],
		moneyList: ['不限', '2000以下', '2000-5000', '5000以上'],
		timeList: ['不限', '7天', '15天', '1个月', '1-6个月', '6-12个月', '12个月以上'],
		allLoaded: false,
		maneyBox: false, // 金额筛选
		timeBox: false, // 期限筛选
		scrollMode: 'auto',
		searchCondition: { // 查询全部参数
			flag: 'all',
			current: 1 // 分页
		}
    }
  },
  methods: {
  	...mapMutations([
		'TYPE_DHH'
	]),
    loadTop() {
	  // 加载更多数据
	  this.init()
	  this.$refs.loadmore.onTopLoaded();
	},
	loadBottom() {
	  // 加载更多数据
	  this.more()
	},
	more() {
		this.searchCondition.current = parseInt(this.searchCondition.current) + 1;
		let params = this.searchCondition;
		getFlowPlatformInfo(params).then(res => {
			if (res.code === 200) {
				// this.msg = res.data;
				for (var i = res.data.length - 1; i >= 0; i--) {
					this.msg.push(res.data[i]);
				}
				if (res.data.length < 14) {
					this.allLoaded = true
				} else {
					this.allLoaded = false
				}
				this.$refs.loadmore.onBottomLoaded()
				// this.$nextTick(() => {
				// 	this.scrollMode = 'touch';
				// })
			}
		})
	},
	moneyClick(index) { // 金钱筛选
		var sp = document.getElementsByClassName('moneySpan')
		for (var i = 0; i < sp.length; i++) {
			sp[i].style.cssText = 'color:#999999;border: 0.01rem solid #9e9e9e;'
		}
		sp[index].style.cssText = 'color:red;border: 0.01rem solid red;'
		this.isactive1 = false
		this.maneyBox = false
		this.leftImg = true
		let params = {
			flag: 'search',
			current: 0,
			limit: index,
			deadLine: '0'
		}
  		getFlowPlatformInfo(params).then(res => {
  			if (res.code === 200) {
  				this.msg = res.data
  			}
  		})
	},
	timeClick(index) { // 时间筛选
		var sp = document.getElementsByClassName('timeSpan')
		for (var i = 0; i < sp.length; i++) {
			sp[i].style.cssText = 'color:#999999;border: 0.01rem solid #9e9e9e;'
		}
		sp[index].style.cssText = 'color:red;border: 0.01rem solid red;'
		this.isactive2 = false
		this.timeBox = false
		this.rightImg = true
		let params = {
			flag: 'search',
			current: 0,
			deadLine: index,
			limit: '0'
		}
  		getFlowPlatformInfo(params).then(res => {
  			if (res.code === 200) {
  				this.msg = res.data
  			}
  		})
	},
	leftClick() { // 贷款金额排序
		this.isactive1 = !this.isactive1
		this.leftImg = !this.leftImg
		this.isactive2 = false
		this.rightImg = true
		this.maneyBox = !this.maneyBox
		this.timeBox = false
	},
	rightClick() {
		this.isactive2 = !this.isactive2
		this.rightImg = !this.rightImg
		this.isactive1 = false
		this.leftImg = true
		this.timeBox = !this.timeBox
		this.maneyBox = false
	},
	close() { // 关闭筛选
		this.isactive1 = false
		this.isactive2 = false
		this.maneyBox = false
		this.timeBox = false
		this.leftImg = true
		this.rightImg = true
	},
	init() { // 获取数据
		this.TYPE_DHH(2)
		let params = {
			flag: 'all',
			current: 1 
		}
  		getFlowPlatformInfo(params).then(res => {
  			if (res.code === 200) {	
  				this.msg = res.data
  				this.allLoaded = false
  				this.searchCondition.current = 1
  			}
  		})
  	},
	into(index) {
		// setStore('id', this.msg[index].id)
		// setStore('pcode', this.msg[index].pcode)
		// this.$router.push({path: '/detailsPage', query: {distail: 1}})
		var userId = getStore('userId')
		let params = {
			userId: userId
		}
		getUser(params).then(res => {
			if (res.code === 200) {
				console.log(this.msg[index])
				parent.location.href = this.msg[index].phttp
			} else {
				this.$router.push({path: '/login', query: {distail: 1}})
			}
		})
    }
  },
  mounted() {
  	this.init()
  }
}
</script>

<style lang="scss" rel="stylesheet/scss">
.attestation {
	flex:1;
	-webkit-flex: 1;
	-moz-flex: 1;
	-ms-flex: 1;
	background: #f3f5f8;
	padding-bottom: 1rem;
	.attestation-head {
		width: 100%;
		height: 0.7rem;
		position: fixed;
		z-index: 10;
		background: white;
		
		.Supermarket {
			display: inline-block;
			border-bottom: 0.01rem solid #d6d6d6;
			width: 100%;
			text-align: center;
			font-size: 0.25rem;
			height: 0.8rem;
			line-height: 0.8rem;
			font-weight: 600;
		}
		.screen {
			width: 100%;
			background: white;
			height: 0.7rem;
			
			.left {
				float: left;
				display: inline-block;
				width: 50%;
				text-align: center;
				height: 0.7rem;
				line-height: 0.7rem;
				
				span {
					font-size: 0.3rem;
					color: #999999;
					vertical-align: middle;
				}
				img {
					width: 0.2rem;
					vertical-align: middle;
					margin-top: 0.05rem;
					margin-left: 0.05rem;
				}
			}
			.left1 {
				float: left;
				display: inline-block;
				width: 50%;
				background: #d84949;
				text-align: center;
				height: 0.7rem;
				line-height: 0.7rem;
				
				span {
					font-size: 0.3rem;
					color: white;
					vertical-align: middle;
				}
				img {
					width: 0.2rem;
					vertical-align: middle;
					margin-top: 0.05rem;
					margin-left: 0.05rem;
				}
			}
			.right {
				display: inline-block;
				width: 50%;
				text-align: center;
				height: 0.7rem;
				line-height: 0.7rem;
				
				span {
					font-size: 0.3rem;
					color: #999999;
					vertical-align: middle;
				}
				img {
					width: 0.2rem;
					vertical-align: middle;
					margin-top: 0.05rem;
					margin-left: 0.05rem;
				}
			}
			
			.right1 {
				display: inline-block;
				width: 50%;
				text-align: center;
				background: #d84949;
				height: 0.7rem;
				line-height: 0.7rem;
				
				span {
					font-size: 0.25rem;
					color: white;
					vertical-align: middle;
				}
				img {
					width: 0.22rem;
					vertical-align: middle;
					margin-top: 0.05rem;
					margin-left: 0.05rem;
				}
			}
			
		}
	}
	
	.content {
		margin-top: 0.7rem;
		// height: 100%;
		background-color: #f2f3f2;
		/* width: 98%;
		margin-left: 1%;
		position: fixed;
		margin-bottom: 1rem;
		overflow: hidden; */
		
		.mint-loadmore {
			width: 100%;
			height:100%;
		}
		.mint-loadmore-content {
			width: 100%;
			height: 100%;
			box-sizing: border-box;
			.mint-loadmore-top {
				margin-top: -0.7rem;
			}
		}
		
		.mint-loadmore-bottom {
			/*margin-top: 4rem;*/
		}
		ul {
			width: 100%;
			height: 100%;
		}
		.d {
			display: inline-block;
			width: 48%;
			margin-left: 0.1rem;
			height: 1.3rem;
			border-radius: 0.1rem;
			background: white;
			margin-bottom: 0.1rem;
			position: relative;
			
			span {
				font-size: 0.2rem;
				color: #333;
			}
			
			span:nth-child(2) {
				font-size: 0.25rem;
				position: absolute;
				top: 0.2rem;
				left: 1.4rem;
				color: #222222;
				font-weight: 400;
			}
			span:nth-child(3) {
				position: absolute;
				top: 0.9rem;
				left: 1.4rem;
				color: #999999;
				
				span {
					color: red;
				}
			}
			
			img:nth-child(1) {
				width: 1rem;
				height: 1rem;
				position: absolute;
				border-radius: 0.1rem;
				left: 0.2rem;
				top: 0.2rem;
			}
		}
		
	}
	.moneyBox {
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,0.2);
		position: fixed;
		top: 0.7rem;
		left: 0;
		z-index: 11;
		
		.left {
			width: 100%;
			background: white;
			
			span {
				display: inline-block;
				border: 0.01rem solid #9e9e9e;
				width: 2.1rem;
				height: 0.7rem;
				border-radius: 0.5rem;
				margin-left: 0.3rem;
				line-height: 0.7rem;
				text-align: center;
				font-size: 0.3rem;
				color: #999999;
				margin-top: 0.3rem;
			}
			span:last-child {
				margin-bottom: 0.3rem;
			}
		}
		.bottom {
			width: 100%;
			height: 100%;
			background: rgba(0,0,0,0.0);
		}
	}
	.timeBox {
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,0.2);
		position: fixed;
		top: 0.7rem;
		left: 0;
		z-index: 11;
		.right {
			width: 100%;
			background: white;
			
			span {
				display: inline-block;
				border: 0.01rem solid #9e9e9e;
				width: 2.1rem;
				height: 0.7rem;
				border-radius: 0.5rem;
				margin-left: 0.3rem;
				line-height: 0.7rem;
				text-align: center;
				font-size: 0.3rem;
				color: #999999;
				margin-top: 0.3rem;
			}
			span:last-child {
				margin-bottom: 0.3rem;
			}
		}
		.bottom {
			width: 100%;
			height: 100%;
			background: rgba(0,0,0,0.0);
		}
	}
}

</style>



